html,
body {
  background-color: #edf0f1;
}

.header {
  padding: 25px 0;
  text-align: center;
  margin-bottom: 15px;
}

.container {
  width: 600px;
  margin: 0 auto;
}

.recipe-list {
  margin-top: 20px;
  padding-bottom: 50px;
}

.recipe {
  margin: 10px 0;
}

.recipe-action {
  float: right;
  text-align: center;
  padding: 1rem;
  width: 50px;
  cursor: pointer;
  color: #2c3e00;
}

.recipe-action:hover,
.recipe-toggle:hover {
  color: #fff;
}

.recipe-toggle {
  display: block;
  padding: 0.75rem 1.25rem;
  cursor: pointer;
}

.recipe-title {
  padding-left: 15px;
  font-size: 1.35em;
}

.list-group-item {
  padding: 0;
}

.list-group-item + .list-group-item {
  border-top-width: 1px;
}

.content-recipe {
  padding: 0.75rem 1.25rem;
  margin-top: 8px;
}

.square-list {
  list-style-type: square;
  margin-bottom: 12px;
  margin-right: 115px;
}

.number-list {
  list-style-type: decimal;
}

.image-input {
  display: none;
}

.image-group {
  height: 50px;
}

.image-preview {
  width: 75px;
  margin-bottom: 10px;
  float: right;
}

.image-recipe {
  width: 100px;
  float: right;
}
